article, aside, details, figcaption, figure, footer, header, hgroup, nav, section, summary {
  display: block; }

audio, canvas, video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden] {
  display: none; }

html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%; }

a:focus {
  outline: thin dotted; }

a:active, a:hover {
  outline: 0; }

h1 {
  font-size: 2em; }

abbr[title] {
  border-bottom: 1px dotted; }

b, strong {
  font-weight: 700; }

dfn {
  font-style: italic; }

mark {
  background: #ff0;
  color: #000; }

code, kbd, pre, samp {
  font-family: monospace, serif;
  font-size: 1em; }

pre {
  white-space: pre-wrap;
  word-wrap: break-word; }

q {
  quotes: \201C \201D \2018 \2019; }

small {
  font-size: 80%; }

sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -.5em; }

sub {
  bottom: -.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

fieldset {
  border: 1px solid silver;
  margin: 0 2px;
  padding: .35em .625em .75em; }

button, input, select, textarea {
  font-family: inherit;
  font-size: 100%;
  margin: 0; }

button, input {
  line-height: normal; }

button, html input[type=button],
input[type=reset], input[type=submit] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled], input[disabled] {
  cursor: default; }

input[type=checkbox], input[type=radio] {
  box-sizing: border-box;
  padding: 0; }

input[type=search] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box; }

input[type=search]::-webkit-search-cancel-button, input[type=search]::-webkit-search-decoration {
  -webkit-appearance: none; }

textarea {
  overflow: auto;
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

body, figure {
  margin: 0; }

legend, button::-moz-focus-inner, input::-moz-focus-inner {
  border: 0;
  padding: 0; }

.clearfix:after {
  visibility: hidden;
  display: block;
  font-size: 0;
  content: " ";
  clear: both;
  height: 0; }

* {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 18px;
  background: #F5F5F6; }

.flex-nav ul {
  list-style: none;
  display: flex;
  height: 65px;
  padding-left: 30px;
  background: #171e27;
  justify-content: flex-end;
  align-items: center; }
  .flex-nav ul li {
    flex: 0 3 140px; }
    .flex-nav ul li a {
      text-decoration: none;
      color: white;
      outline: none; }
    .flex-nav ul li a:hover {
      color: #cecece; }
    .flex-nav ul li i.fa {
      margin-right: 10px; }
  .flex-nav ul li.flex-growing {
    flex-grow: 1; }
  .flex-nav ul li.btn-login div {
    cursor: pointer;
    color: white; }
    .flex-nav ul li.btn-login div:hover {
      color: #cecece; }

main {
  font-size: 50px;
  color: #171e27;
  max-width: 1300px;
  margin: 0 auto;
  padding-top: 80px;
  padding-left: 50px; }
  main h1 {
    color: white;
    text-shadow: 2px 2px 8px #171e27; }
  main h3 {
    padding-top: 40px; }
  main .coffee-cup {
    position: absolute;
    z-index: -1;
    width: 450px;
    height: 450px;
    top: 35%;
    left: 60%; }

.news-container {
  max-width: 1000px;
  margin: 0px auto;
  display: flex;
  flex-wrap: wrap; }
  .news-container article {
    flex: 1 1 calc(100% - 50px);
    margin: 50px 50px 0px 50px;
    font-size: 23px;
    line-height: 35px;
    height: 500px;
    overflow: hidden;
    text-align: justify; }
    .news-container article h3 {
      font-size: 30px;
      padding: 20px 30px;
      background-color: #3e454f;
      color: white;
      display: flex; }
      .news-container article h3 span {
        flex: 0 1 220px; }
        .news-container article h3 span a:hover {
          color: rgba(255, 255, 255, 0.48); }
      .news-container article h3 .article-date {
        flex-grow: 1;
        font-size: 18px;
        font-weight: normal;
        text-align: right;
        color: rgba(255, 255, 255, 0.48); }
      .news-container article h3 .article-bookmark {
        flex-basis: 80px;
        text-align: right;
        position: relative; }
        .news-container article h3 .article-bookmark .tooltip {
          display: inline-block;
          padding: 15px 15px;
          text-align: left;
          border-radius: 5px;
          position: absolute;
          color: white;
          background: #38588c;
          top: -5px;
          right: 50px;
          font-size: 18px; }
        .news-container article h3 .article-bookmark:hover {
          color: #E1E2E1;
          cursor: pointer; }
    .news-container article .article-button {
      text-decoration: none;
      color: inherit; }
      .news-container article .article-button div {
        padding: 20px 80px;
        background-color: #E1E2E1;
        transition: all 200ms; }
        .news-container article .article-button div:hover {
          transform: scale(1.03); }

.single-article {
  max-width: 1000px;
  margin: 0 auto;
  font-size: 25px;
  line-height: 40px;
  text-align: justify; }
  .single-article h2 {
    font-size: 45px;
    padding-top: 40px;
    padding-bottom: 30px;
    text-align: center; }
  .single-article .single-article-bookmark {
    position: fixed;
    top: 100px;
    right: 300px;
    font-size: 30px; }
    .single-article .single-article-bookmark .tooltip {
      display: inline-block;
      text-align: left;
      padding: 15px 15px;
      border-radius: 5px;
      position: absolute;
      color: white;
      background: #38588c;
      top: -5px;
      right: 50px;
      font-size: 18px; }
    .single-article .single-article-bookmark:hover {
      color: #cecece;
      cursor: pointer; }

#login-form {
  max-width: 300px;
  margin: 200px auto;
  height: 200px;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  justify-content: space-around; }
  #login-form * {
    flex: 0 0 40px;
    border-radius: 6px;
    border: 2px solid #dddddd;
    outline: none;
    padding-left: 25px;
    transition: all 200ms; }
  #login-form #email:focus {
    border-color: grey; }
  #login-form #password:focus {
    border-color: grey; }
  #login-form #submit-btn {
    padding: 0;
    color: #545454;
    background-color: #E1E2E1;
    border-color: #a8a8a8; }
    #login-form #submit-btn:hover {
      background-color: #cecece; }

.profile-container {
  max-width: 800px;
  min-height: 400px;
  margin: 30px auto;
  display: flex;
  flex-wrap: wrap; }
  .profile-container div {
    flex: 1 1 100%; }
    .profile-container div span {
      font-weight: bold;
      color: #171e27; }
  .profile-container .user-img {
    flex-grow: 1;
    width: 100px;
    height: 100px; }
  .profile-container .user-name {
    text-align: center;
    color: #171e27; }
  .profile-container div.social-links {
    flex-basis: 50px;
    align-self: flex-end;
    font-size: 25px;
    display: flex;
    justify-content: center; }
    .profile-container div.social-links a {
      color: #171e27; }
      .profile-container div.social-links a i {
        transition: all 200ms; }
        .profile-container div.social-links a i:hover {
          transform: scale(1.2); }

.bookmark-block {
  display: inline-block;
  background-color: #44597c;
  padding: 3px 10px;
  margin-right: 10px;
  margin-bottom: 5px; }
  .bookmark-block a {
    text-decoration: none;
    color: white; }
    .bookmark-block a:hover {
      color: #cecece; }
  .bookmark-block i {
    margin-left: 10px;
    color: white;
    cursor: pointer; }
    .bookmark-block i:hover {
      color: #f44b42; }

.error-message {
  position: absolute;
  top: 12%;
  left: 39%;
  width: 400px;
  height: 100px;
  display: grid;
  justify-content: center;
  align-items: center;
  background-color: #ff928c;
  border: 2px solid #f44b42;
  border-radius: 5px;
  color: white; }

.loading {
  margin: 0 auto;
  margin-top: 50px;
  border: 10px solid white;
  border-top: 10px solid #171e27;
  border-radius: 50%;
  width: 80px;
  height: 80px;
  animation: spin 1.5s linear infinite; }

@-webkit-keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@keyframes spin {
  0% {
    transform: rotate(0deg); }
  100% {
    transform: rotate(360deg); } }

@media screen and (max-width: 650px) {
  main {
    padding-top: 10px;
    font-size: 35px; }
    main h1 {
      font-size: 55px; }
    main .coffee-cup {
      width: 300px;
      height: 300px;
      top: 50%;
      left: 15%; } }
