@import '_normilize';

$navcolor: #171e27;
$offwhite: #F5F5F6;
$articletitle: #3e454f;
$articletext: #E1E2E1;
$hovernavcolor: #cecece;

body {
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  	-webkit-font-smoothing: antialiased;
  	-moz-osx-font-smoothing: grayscale;
  	font-size: 18px;
  	background: $offwhite;
}

.flex-nav ul {
	list-style: none;
	display: flex;
	height: 65px;
	padding-left: 30px;
	background: $navcolor;
	justify-content: flex-end;
	align-items: center;
 
	li {
		flex: 0 3 140px;

		a {
			text-decoration: none;
			color: white;
			outline: none;
		}

		a:hover {
			color: $hovernavcolor;
		}

		i.fa {
			margin-right: 10px;
		}
	}

	li.flex-growing {
		flex-grow: 1;
	}

	li.btn-login div {
		cursor: pointer;
		color: white;

		&:hover {
			color: $hovernavcolor;
		}
	}
}

main {
	font-size: 50px;
	color: $navcolor;
	max-width: 1300px;
	margin: 0 auto;
	padding-top: 80px;
	padding-left: 50px;

	h1 {
		color: white;
		text-shadow: 2px 2px 8px $navcolor;
	}

	h3 {
		padding-top: 40px;
	}

	.coffee-cup {
		position: absolute;
		z-index: -1;
		width: 450px;
		height: 450px;
		top: 35%;
		left: 60%;
	}
}

.news-container {
	max-width: 1000px;
	margin: 0px auto;
	display: flex;
	flex-wrap: wrap;

	article {
		flex: 1 1 calc(100% - 50px);
		margin: 50px 50px 0px 50px;
		font-size: 23px;
		line-height: 35px;
		height: 500px;
		overflow: hidden;
		text-align: justify;

		h3 {
			font-size: 30px;
			padding: 20px 30px;
			background-color: $articletitle;
			color: white;
			display: flex;
			
			span {
				flex: 0 1 220px;

				a:hover {
					color: rgba(255,255,255,0.48);
				}
			}

			.article-date {
				flex-grow: 1;
				font-size: 18px;
				font-weight: normal;
				text-align: right;
				color: rgba(255,255,255,0.48);
			}

			.article-bookmark {
				flex-basis: 80px;
				text-align: right;
				position: relative;

				.tooltip {
					display: inline-block;
					padding: 15px 15px;
					text-align: left;
					border-radius: 5px;
					position: absolute;
					color: white;
					background: #38588c;
					top: -5px;
					right: 50px;
					font-size: 18px;
				}

				&:hover {
					color: $articletext;
					cursor: pointer;
				}
			}
		}

		.article-button {
			text-decoration: none;
			color: inherit;

			div {
				padding: 20px 80px;
				background-color: $articletext;
				transition: all 200ms;

				&:hover {
					transform: scale(1.03);
				}
			}
		}
	}
}

.single-article {
	max-width: 1000px;
	margin: 0 auto;
	font-size: 25px;
	line-height: 40px;
	text-align: justify;

	h2 {
		font-size: 45px;
		padding-top: 40px;
		padding-bottom: 30px;
		text-align: center;
	}

	.single-article-bookmark {
		position: fixed;
		top: 100px;
		right: 300px;
		font-size: 30px;

		.tooltip {
			display: inline-block;
			text-align: left;
			padding: 15px 15px;
			border-radius: 5px;
			position: absolute;
			color: white;
			background: #38588c;
			top: -5px;
			right: 50px;
			font-size: 18px;
		}

		&:hover {
			color: $hovernavcolor;
			cursor: pointer;
		}
	}
}

#login-form {
	max-width: 300px;
	margin: 200px auto;
	height: 200px;
	font-size: 16px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;

	* {
		flex: 0 0 40px;
		border-radius: 6px;
		border: 2px solid rgb(221, 221, 221);
		outline: none;
		padding-left: 25px;
		transition: all 200ms;
	}

	#email:focus {
		border-color: grey;
	}

	#password:focus {
		border-color: grey;
	}

	#submit-btn {
		padding: 0;
		color: #545454;
		background-color: $articletext;
		border-color: #a8a8a8;

		&:hover {
			background-color: #cecece;
		}
	}
}

.profile-container {
	max-width: 800px;
	min-height: 400px;
	margin: 30px auto;
	display: flex;
	flex-wrap: wrap;

	div {
		flex: 1 1 100%;

		span {
			font-weight: bold;
			color: $navcolor;
		}
	}

	.user-img {
		flex-grow: 1;
		width: 100px;
		height: 100px;
	}	

	.user-name {
		text-align: center;
		color: $navcolor;
	}

	div.social-links {
		flex-basis: 50px;
		align-self: flex-end;
		font-size: 25px;
		display: flex;
		justify-content: center;

		a {
			color: $navcolor;
			
			i {
				transition: all 200ms;	

				&:hover {
					transform: scale(1.2);
				}
			}
		}
	}
}

.bookmark-block {
	display: inline-block;
	background-color: #44597c;
	padding: 3px 10px;
	margin-right: 10px;
	margin-bottom: 5px;

	a {
		text-decoration: none;
		color: white;

		&:hover {
			color: $hovernavcolor;
		}
	}

	i {
		margin-left: 10px;
		color: white;
		cursor: pointer;

		&:hover {
			color: #f44b42;
		}
	}
}

.error-message {
	position: absolute;
	top: 12%;
	left: 39%;
	width: 400px;
	height: 100px;
	display: grid;
	justify-content: center;
	align-items: center;
	background-color: #ff928c;
	border: 2px solid #f44b42;
	border-radius: 5px;
	color: white;
}

.loading {
    margin: 0 auto;
    margin-top: 50px;
    border: 10px solid white;
    border-top: 10px solid $navcolor;
    border-radius: 50%;
    width: 80px;
    height: 80px;
    animation: spin 1.5s linear infinite;
}

@-webkit-keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

@media screen and (max-width: 650px) {
	main {
		padding-top: 10px;
		font-size: 35px;

		h1 {
			font-size: 55px;
		}

		.coffee-cup {
			width: 300px;
			height: 300px;
			top: 50%;
			left: 15%;
		}
	}
}